import React, {useEffect} from 'react'
import Data from '../Data.js'

function Form() {

 

 const [meme, setMeme] = React.useState({
  topText: "",
  bottomText: "",
  randomImage: "http://i.imgflip.com/1bij.jpg" 
})

/* eslint-disable no-unused-vars */
const [allMemeImages, setAllMemeImages] = React.useState(Data)
/* eslint-enable no-unused-vars */

function handleClick() {
  const memesArray = allMemeImages.data.memes
  const randomNumber = Math.floor(Math.random() * memesArray.length)
  const url = memesArray[randomNumber].url
  setMeme(prevMeme => ({
      ...prevMeme,
      randomImage: url
  }))
  
}

function handleChange (event) {

  const {name, value} = event.target

  setMeme(prevMeme => ({
    ...prevMeme,
    [name]: value
   
}))

}



useEffect(() => {
   handleClick();     // Call this function to set a random image on component mount
   // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []); 


  return (
    <>
   <form>
    <div className="row" style={{padding: '5%'}}>
  <div className="col">

    <input type="text" className="form-control" placeholder="First Text" name='topText' aria-label="First name" 
    value={meme.topText}
     onChange={handleChange}
   
    />
  </div>


  <div className="col">

    <input type="text" className="form-control" placeholder="Last Text" name='bottomText' aria-label="Last name"
    value={meme.bottomText} 
    onChange={handleChange}
     
     />
  </div>

  <div className="row">
  <div className="col div1">
    <button className="form-control btn btn-success" onClick={handleClick}>Get new img</button>
  </div>
</div>
</div>

</form>

<div className="meme">
<img src={meme.randomImage}   alt='nothing' className='rounded mx-auto d-block' />
<h2 className="meme--text top">{meme.topText}</h2>
  <h2 className="meme--text bottom">{meme.bottomText}</h2>
  </div>

 </>
  )
}

export default Form
