import React from 'react'
import logo from '../images/download.png'
function Navbar() {
  return (
    <>
    <nav className="nav">
  <div className="container-fluid">
    <a className="navbar-brand" href="/">
      <img src={logo} alt="Logo" width="50" height="50" style={{background: '#3CB371'}} className="d-inline-block align-text-top"></img>
      Meme-Generator
    </a>
  </div>
</nav>
      
    </>
  )
}

export default Navbar
